import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';
import injectSheet from 'react-jss';

import {
    NamespacesConsumer
} from 'react-i18next';

import {
    Row,
    Col,
    Grid
} from 'react-flexbox-grid';

import SkillBar from './SkillBar';
import CircularSkillBar from './CircularSkillBar';

import skillsStyle from '../assets/jss/components/skillsStyle';

class Skills extends React.Component {

    render() {
        const {
            classes,
            className
        } = this.props;

        return (
            <NamespacesConsumer>
                {(t, { i18n }) =>
                    <div className={classNames(className, classes.container)}>
                        <div className={classes.curlyBrace}>
                            <div className={classNames(classes.brace, classes.leftBrace)} />
                            <div className={classNames(classes.brace, classes.rightBrace)} />
                        </div>
                        <Grid fluid className={classes.skills}>
                            <Row>
                                <Col xs={12} md={6}>
                                    <SkillBar className={classes.skillBar} label="HTML" proficiency={97} color="primary" />
                                    <SkillBar className={classes.skillBar} label="CSS" proficiency={84} color="primary" />
                                    <SkillBar className={classes.skillBar} label="Javascript" proficiency={92} color="primary" />
                                    <SkillBar className={classes.skillBar} label="React" proficiency={87} color="primary" />
                                    <SkillBar className={classes.skillBar} label="PHP" proficiency={90} color="primary" />
                                </Col>
                                <Col xs={12} md={6}>
                                    <SkillBar className={classes.skillBar} label="Ionic" proficiency={81} color="primary" />
                                    <SkillBar className={classes.skillBar} label="C#" proficiency={85} color="primary" />
                                    <SkillBar className={classes.skillBar} label="Java" proficiency={90} color="primary" />
                                    <SkillBar className={classes.skillBar} label="NodeJS" proficiency={86} color="primary" />
                                </Col>
                            </Row>
                            <Row center="xs" className={classes.languages}>
                                <Col xs={12} md={6} className={classes.portugueseContainer}>
                                    <CircularSkillBar
                                        proficiency={100}
                                        label="Português"
                                    />
                                </Col>
                                <Col xs={12} md={6} className={classes.englishContainer}>
                                    <CircularSkillBar
                                        proficiency={85}
                                        label="Inglês"
                                    />
                                </Col>
                            </Row>
                        </Grid>
                    </div>
                }
            </NamespacesConsumer>
        );
    }
}

Skills.propTypes = {
    className: PropTypes.string,
    classes: PropTypes.object.isRequired
};

export default injectSheet(skillsStyle)(Skills);
