import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';
import injectSheet from 'react-jss';

import P from './P';

import skillBarStyle from '../assets/jss/components/skillBarStyle';

class SkillBar extends React.Component {

    render() {
        const {
            label,
            color,
            classes,
            className,
            proficiency
        } = this.props;

        return (
            <div className={classNames(className, classes.container)}>
                <P className={classes.label}>{label}</P>
                <P className={classes.proficiency}>{proficiency}%</P>
                <div className={classNames(classes.skill, classes[color])} />
            </div>
        );
    }
}

SkillBar.propTypes = {
    className: PropTypes.string,
    label: PropTypes.string.isRequired,
    color: PropTypes.string.isRequired,
    classes: PropTypes.object.isRequired
};

export default injectSheet(skillBarStyle)(SkillBar);
