import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';
import injectSheet from 'react-jss';

import CircularProgressbar from 'react-circular-progressbar';

import * as theme from '../assets/jss/theme';

import circularSkillBarStyle from '../assets/jss/components/circularSkillBarStyle';

class CircularSkillBar extends React.Component {

    render() {
        const {
            label,
            color,
            classes,
            className,
            proficiency
        } = this.props;

        return (
            <CircularProgressbar
                text={label}
                strokeWidth={12}
                percentage={proficiency}
                className={classNames(className, classes.container)}
                styles={{
                    path: {
                        stroke: color ? theme[color] : theme.primaryColor
                    },
                    trail: {
                        stroke: theme.grayColor
                    },
                    text: {
                        ...theme.defaultFont,
                        fontSize: '12px',
                        fill: color ? theme[color] : theme.primaryColor
                    }
                }}
            />
        );
    }
}

CircularSkillBar.propTypes = {
    color: PropTypes.string,
    className: PropTypes.string,
    label: PropTypes.string.isRequired,
    classes: PropTypes.object.isRequired
};

export default injectSheet(circularSkillBarStyle)(CircularSkillBar);
